.main {
    height: 100vh;
    width: 80%;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    gap: 15rem;
}

li {
    color: gray;
    padding-top: 3px;
    font-size: 15px;
}

.message {
    padding: 5px;
    border: 3px solid green;
    border-radius: 5px;
    color: green;
    background-color: lightgray;
    margin-bottom: 4rem;
}

.error {
    padding: 5px;
    border: 3px solid red;
    border-radius: 5px;
    color: red;
    background-color: lightgray;
    margin-bottom: 4rem;
}